}
#active-tab-btn{
  @apply bg-[#0A1C2B] text-[#ffffff];
}
.ant-select{
  width: 100% !important;
}
.ant-select-selector,.ant-picker,.ant-input{
  border-radius: 1.5rem !important;
  width: 100% !important;
  border-color: #707070 !important;

}
.ant-select-arrow{
  transition: .4s ease-in-out !important;
  font-size: 12px !important;
}
.ant-select-open .ant-select-arrow{
  transform: rotate(-180deg) !important;
}
.ant-select-selection-placeholder,.ant-picker-input input::placeholder,.ant-input::placeholder{
  font-size: 12px !important;
  font-weight: 600 !important;
}
.icon-search-card{
  box-shadow: 0 0px 20px #0000004D;
}





.logsc{
  background-image: url('../src/assets/images/3.jpg');
}
.custon_checkbox{
  position: relative;
}
.custon_checkbox::before{
    content: '';
    width: 18px;
    height: 18px;
    border: 1px solid #707070;
    display: block;
    position: absolute;
   left: -4px;
    border-radius: 50%;
    cursor: pointer;
    background: #fff;
}
.custon_checkbox::after{
  content: '';
  width: 12px;
  height: 12px;
  background: #F24D5D;
  display: block;
  position: absolute;
  top: 3px;
  left: -1px;
  border-radius: 50%;
  opacity: 0;
  transition: .4s ease-in-out;
  
}
.custon_checkbox:checked::after{
  opacity: 1;
}
